<template>
  <div class="container">
    <div class="row mt-4">
      <div class="col mb-3">
        <h3>Xem truoc</h3>
        <!-- Syntax -->
        <Syntax />
        <div class="box" id="idChenContent">
          <h3><div v-html="post.tieude"></div></h3>
          <hr />
          <img :src="post.img" alt="" />
          <div v-html="post.mNoidung"></div>
        </div>
      </div>
      <div class="col mt-2">
        <h3>Tao bai viet</h3>
        <div class="addpost mt-3 mb-1">
          <div class="mb-3">
            <label for="exampleFormControlInput1" class="form-label"
              >Tieu de</label
            >
            <input
              type="text"
              class="form-control"
              v-model="post.tieude"
              maxlength="150"
              @input="xemtruoc"
            />
          </div>
          <div class="mb-3">
            <label for="exampleFormControlInput1" class="form-label"
              >Anh banner</label
            >
            <input
              type="text"
              class="form-control"
              v-model="post.img"
              maxlength="150"
              @input="xemtruoc"
            />
          </div>
          <div class="mb-3">
            <label for="exampleFormControlTextarea1" class="form-label"
              >Noi dung</label
            >
            <textarea
              class="form-control"
              id="exampleFormControlTextarea1"
              rows="3"
              v-model="post.noidung"
              style="height: 500px"
              @input="xemtruoc"
            ></textarea>
          </div>
        </div>

        <div class="row gap-2 mx-auto">
          <!-- Button trigger modal -->
          <div class="col">
            <button class="btn btn-secondary" @click="xemtruoc">
              Xem truoc
            </button>
          </div>
          <div class="col">
            <button
              class="col btn btn-primary"
              type="button"
              @click="addPost"
              data-bs-toggle="modal"
              data-bs-target="#applyPost"
              v-if="!post.id"
            >
              Dang bai viet
            </button>
            <button
              class="col btn btn-primary"
              type="button"
              @click="editPost"
              data-bs-toggle="modal"
              data-bs-target="#applyPost"
              v-else
            >
              Cap nhat bai viet
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal -->
    <div
      class="modal fade"
      id="applyPost"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">...</div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close
            </button>
            <button type="button" class="btn btn-primary">Save changes</button>
          </div>
        </div>
      </div>
    </div>

    <div style="height: 50px"></div>
  </div>
</template>

<script>
import {
  collection,
  addDoc,
  getFirestore,
  getDocs,
  updateDoc,
  doc,
} from "firebase/firestore";
import Syntax from "../components/MarkdownSyntax.vue";
import { mapState } from 'vuex'

export default {
  components: {
    Syntax,
  },
  computed: mapState(['user']),
  data: () => {
    return {
      post: {
        tieude: "",
        noidung: "",
        mTieude: "",
        mNoidung: "",
        slug: "Slug",
        img: null,
        time: "",
        user:''
      },
    };
  },
  created() {
    if (this.$route.params.slug) {
      this.post.id = this.$route.params.slug;
      (async () => {
        const querySnapshot = await getDocs(
          collection(getFirestore(), "posts")
        );
        querySnapshot.forEach((doc) => {
          if (doc.id == this.post.id) {
            this.post.tieude = doc.data().title;
            this.post.noidung = doc.data().mdcontent;
            this.post.img = doc.data().img
          }
        });
      })();
    }
  },
  methods: {
    xemtruoc: function (post, event) {
      var markdown = require("markdown").markdown;
      this.post.mTieude = "###" + this.post.tieude;
      this.post.mNoidung = markdown.toHTML(this.post.noidung);
      var str = this.post.tieude.toLowerCase();
      //Đổi ký tự có dấu thành không dấu
      str = str
        .replace(/á|à|ả|ạ|ã|ă|ắ|ằ|ẳ|ẵ|ặ|â|ấ|ầ|ẩ|ẫ|ậ/gi, "a")
        .replace(/é|è|ẻ|ẽ|ẹ|ê|ế|ề|ể|ễ|ệ/gi, "e")
        .replace(/i|í|ì|ỉ|ĩ|ị/gi, "i")
        .replace(/ó|ò|ỏ|õ|ọ|ô|ố|ồ|ổ|ỗ|ộ|ơ|ớ|ờ|ở|ỡ|ợ/gi, "o")
        .replace(/ú|ù|ủ|ũ|ụ|ư|ứ|ừ|ử|ữ|ự/gi, "u")
        .replace(/ý|ỳ|ỷ|ỹ|ỵ/gi, "y")
        .replace(/đ/gi, "d")
        //Xóa các ký tự đặt biệt
        .replace(
          /\`|\~|\!|\@|\#|\||\$|\%|\^|\&|\*|\(|\)|\+|\=|\,|\.|\/|\?|\>|\<|\'|\"|\:|\;|_/gi,
          ""
        )
        //Đổi khoảng trắng thành ký tự gạch ngang
        .replace(/ /gi, "-")
        //Đổi nhiều ký tự gạch ngang liên tiếp thành 1 ký tự gạch ngang
        //Phòng trường hợp người nhập vào quá nhiều ký tự trắng
        .replace(/\-\-\-\-\-/gi, "-")
        .replace(/\-\-\-\-/gi, "-")
        .replace(/\-\-\-/gi, "-")
        .replace(/\-\-/gi, "-")
        .replace(/\@\-|\-\@|\@/gi, "");
      //In slug ra textbox có id “slug”
      this.post.slug = str;
    },
    addPost: function () {
      var currentDate = new Date();
      var day = currentDate.getDate();
      var month = currentDate.getMonth() + 1;
      var year = currentDate.getFullYear();
      var hour = currentDate.getHours();
      var minute = currentDate.getMinutes();
      this.post.time = hour + ':' + minute + ' - ' + day + "/" + month + "/" + year;
      (async () => {
        try {
          await addDoc(collection(getFirestore(), "posts"), {
            title: this.post.tieude,
            slug: this.post.slug,
            mdcontent: this.post.noidung,
            img: this.post.img,
            time: this.post.time,
            user: this.user.name
          });
          console.log("Document written");
        } catch (e) {
          console.error("Error adding document: ", e);
        }
      })();
    },
    editPost() {
      (async () => {
        try {
          await updateDoc(doc(getFirestore(), "posts", this.post.id), {
            title: this.post.tieude,
            slug: this.post.slug,
            mdcontent: this.post.noidung,
            img: this.post.img
          });
          console.log("Document Edited");
        } catch (e) {
          console.error("Error edit document: ", e);
        }
      })();
    },
  },
};
</script>

